<template>
  <div class="container">
    <button class="btn btn-success btn-block mt-5" v-on:click="openNewUserForm">Agregar Nuevo Usuario</button>
    <table class="table text-center mt-4 table-sm">
        <tbody>
          <tr v-for="user in users" v-bind:key="user._id">
            <td class="d-none d-sm-block">{{ user.username }}</td>
            <td>{{ user.email }}</td>
            <td>{{ user.isActive | active }}</td>
            <td><button class="btn btn-danger btn-sm"><i class="fa fa-trash"></i></button></td>
          </tr>
        </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios'
import _ from 'lodash'

export default {
  name: 'users',
  data(){
    return {
      users: []
    }
  },
  created(){
    this.getAllUsers()
  },
  methods: {
    getAllUsers () {
      this.users = []
      this.token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
      axios.post('/users/all', {}, {
        headers: {
          'Content-Type': 'application/json',
          'X-Auth': this.token.token
        }
      })
        .then(resp => {
          if (resp.status === 200) {
            this.users = _.filter(resp.data, o => { return !o.isMaster })
          }
        }).catch(() => {})
    },
    openNewUserForm(){
      this.$router.push('/new_user');
    }
  },
  filters: {
    active(isActive){
      if(isActive){
        return 'Si'
      } 
      return 'No'
    }
  }
}
</script>

